.meals-Page {
    .mealPage-container{
        max-width: 100% !important;
        justify-content: center;
        flex-wrap: wrap;
        justify-content: left;
        display: flex;

        .modal-content{
            margin: auto;
            margin-top: 25%;
            // margin: auto;
        }

        .mealContainer{
            width: 90%;
            margin: auto;

            .meal-card{
                justify-content: center;
                padding: 26px 42px;
                box-shadow: 2px 2px 12px 0px #c6c6c6;
                margin-bottom: 30px;
                cursor: pointer;
            }
        }
    }

    .loadmore-section{
        text-align: center;
        .btn-loadmore{
            padding: 5px 20px;
            border-radius: 5px;
            font-size: 15px;
            background: dodgerblue;
            color: aliceblue;
            font-weight: 700;
        }
    }
}

.meal-title{
    font-size: 20px;
    color: blue;
    font-weight: 500;
    margin-left: 50px;
    margin-top: 20px;
}

.modal-100w {
    max-width: 90% !important;
    height: 400px;
    margin: auto !important;
} 

@media screen and (max-width: 800px) {
    .modal-100w {
      height: auto;
      width: 90% !important;
      max-width: 900px !important;
      margin: auto !important;
      margin-top: 25%!important
    }
}
